@import 'tools/_mixins'
@import 'tools/_helpers'
@import 'tools/_media_queries'
@import 'tools/_normalize'
@import 'tools/_settings'
#choose-meat
  .container
    .header
      justify-content: space-between
      .back
        margin-left: 10px
      .logo
        font-family: 'JackDaniels CondensedBold'
        color: white 
        font-size: 24px
        +mobile-small()
          font-size: 20px
    .main 
      background: url('../images/bg.jpg')
      background-size: cover
      min-height: calc(100vh - 61px)
      height: 100%
      .slider
        color: white
        border-bottom: 1px solid #4c4d4d
        .slick-next
          right: Rem(20)
          z-index: 5
          top: 80px
          bottom: rem(43)
          height: 50px
          width: 50px
          transform: rotate(180deg)
          &:before
            content: url('../images/btn_arrow.1.svg')
        .slick-prev
          left: Rem(20)
          height: 50px
          width: 50px
          top: 80px
          bottom: rem(43)
          z-index: 5

          &:before
            content: url('../images/btn_arrow.1.svg')
        .slick-dots
          margin: 0
          bottom: -25px
          .slick-active
            background: #E19541
          li 
            background: white
            border-radius: 50%
            width: 10px
            height: 10px
        .slide
          text-align: center
          padding: 40px 0 0 0
          outline: none
          .title
            @extend .column
            align-items: center
            font-size: 50px
            line-height: 85px
            font-family: 'SPSL Clarendon'
            font-weight: bold
            padding: 0 10px 0 10px
            +mobile-small()
              font-size: 30px
            .txt
              min-width: 190px
              +mobile-small()
                min-width: 150px
              &:first-of-type
                line-height: 45px
                border-bottom: 1px solid #4c4d4d
              &:last-of-type
                margin-top: 8px
                color: #E19541
                font-size: 85px
                border-top: 1px solid #4c4d4d
                +mobile-small()
                  font-size: 55px
          .main-text
            font-family: 'JackDaniels CondensedBold'
            font-size: 16px
            margin-top: 10px
            
            padding: 0 10px 50px 10px
