@import 'tools/_mixins'
@import 'tools/_helpers'
@import 'tools/_media_queries'
@import 'tools/_normalize'
@import 'tools/_settings'
#places-lazy-items
  .container
    .spanner
      font-family: 'SPSL Clarendon'
      font-style: italic
      font-weight: bold
      font-size: 35px
      line-height: 35px
      +mobile-small()
        font-size: 30px
    .header
      justify-content: space-between
      .back
        margin-left: 10px
      .logo
        font-family: 'JackDaniels CondensedBold'
        color: white 
        font-size: 24px
        +mobile-small()
          font-size: 14px
    .main 
      background: url('../images/bg.jpg') repeat-y 
      background-size: cover
      height: 100%
      @extend .row
      flex-wrap: wrap
      .flex-item
        width: 50%
        height: 318px
        border-bottom: 1px solid #4c4d4d
        position: relative
        color: white
        text-decoration: none
        background: black
        @extend .column
        justify-content: center
        align-items: center
        +mobile-small()
          height: 220px
        .opacity
          width: 100%
          height: 100%
          position: absolute
          background: black
          opacity: 0.5
          top: 0
          left: 0
        svg 
          min-width: 20px
          max-width: 20px
          margin-right: 5px
          path 
            fill: #E19541 !important
          +mobile-small()
            min-width: 10px
            max-width: 10px
          &:nth-of-type(2)
            transform: rotate(180deg)
            margin: 0 
            margin-left: 5px
        .title
          @extend .row
          align-items: center
          margin-top: Rem(40)
          font-family: 'JackDaniels CondensedBold'
          font-size: 30px
          font-style: normal
          font-weight: bold
          line-height: 30px
          z-index: 2
          text-align: center
          +mobile-small()
            font-size: 18px !important
            
            line-height: 20px !important

        

        .image
          position: absolute
          width: 100%
          height: 100%
          top: 0
          left: 0
          background: url('../images/lazy.jpg') no-repeat center center
          background-size: cover
        &:nth-of-type(1n+1)  
          border-right: 1px solid #4c4d4d
        &:nth-of-type(2n)  
