@import 'tools/_mixins'
@import 'tools/_helpers'
@import 'tools/_media_queries'
@import 'tools/_normalize'
@import 'tools/_settings'

#chosen-places

  .popup
    display: none
    position: fixed
    width: 100%
    height: 100%
    background: rgba(0,0,0,0.5)
    z-index: 100
    display: flex
    
    .form
      margin: auto
      color: white
      
      font-family: 'JackDaniels CondensedBold'
      font-size: 20px
      @extend .column
      align-items: center
      .counter 
        width: 100%
        @extend .row
        align-items: center
        span 
          font-size: 70px
          line-height: 70px
      .button
        margin-top: 10px
        padding: 20px 0 15px 0
        width: 100%
        text-align: center
        font-size: 35px
        background: #E19541
        border-radius: 10px
  .container
    .header
      justify-content: space-between
      .back
        margin-left: 10px
      .logo
        font-family: 'JackDaniels CondensedBold'
        color: white 
        font-size: 24px
    .main-inner
      background: url('../images/bg.jpg') repeat-y 
      background-size: cover
      min-height: calc(100vh - 62px)
      
      .flex-inner
        height: 100%
        background: none
        @extend .column
        
        .flex-item
          width: 100%
          min-height: 45vh
          position: relative
          text-align: center
          &:first-of-type
            background: black
            
            .image
              position: absolute
              left: 0 
              top: 0
              width: 100%
              height: 100%
              background: url('../images/big-meat.png') no-repeat 100% 100%
              background-size: cover
              z-index: 1
            .text
              z-index: 5
              color: white
              position: absolute
              left: 50%
              top: 50%
              transform: translate(-50%, -180%)
              font-family: 'SPSL Clarendon'
              .title
                @extend .row
                align-items: center
                font-size: 40px
                font-style: italic
                svg
                  margin-right: 5px
                  path 
                    fill: rgb(225, 149, 65) !important
                  &:last-of-type
                    transform: rotate(180deg)
                    margin-left: 5px
              .txt
                font-size: 62px
                line-height: 70px
            .ui
              position: absolute
              bottom: 3%
              right: 5%
              z-index: 19
              @extend .column
              svg 
                height: 60px
                width: 50px
          &:last-of-type
            font-family: 'JackDaniels CondensedBold'
            color: white
          
            @extend .column
            justify-content: flex-start
            align-items: center
            .active
              display: block
            svg 
              width: 80px
              margin-top: 10px
              path 
                fill: rgb(225, 149, 65) !important
            .nav-local
              .ch 
                text-decoration: underline
              @extend .row

              font-size: 25px
              margin-top: 30px
              width: 65%
              +mobile-small()
                width: 80%
                font-size: 20px
            .inner
              font-size: 25px
              width: 80%
              margin: 0
              
              @extend .column
              align-items: center
              line-height: 40px
              +mobile-small()
                font-size: 20px
              .contant
                display: none
                @extend .column
                .title
                  color: #E19541
                .txt
                  font-size: 18px
                  line-height: 18px
