@import 'tools/_mixins'
@import 'tools/_helpers'
@import 'tools/_media_queries'
@import 'tools/_normalize'
@import 'tools/_settings'

#calculate
  .container
    .header
      justify-content: space-between
      .back
        margin-left: 10px
      .logo
        font-family: 'JackDaniels CondensedBold'
        color: white 
        font-size: 24px
        +mobile-small()
          font-size: 14px
    .main 
      background: url('../images/bg.jpg') repeat-y 
      background-size: cover
      height: calc(100vh - 62px)
      @extend .column
      .flex-item
        flex: 5 1 auto
        display: flex 
        text-decoration: none
        color: white
        .txt
          margin: auto
          text-align: center
          font-family: 'SPSL Clarendon'
          font-weight: bolder
          
          font-size: 25px
          line-height: 45px
          +mobile-small()
            font-size: 20px  
            line-height: 45px  
          span 
            color: #E49741
            font-size: 55px
            line-height: 65px
            +mobile-small()
              font-size: 40px  
              line-height: 45px          
          .italic
            color: white
            font-style: italic
            font-size: 25px
            line-height: 25px
            @extend .row
            align-items: center
            justify-content: center
            svg 
              width: 20px
              margin-right: 10px
              &:last-of-type
                transform: rotate(180deg)
                margin-left: 10px
        &:nth-child(2)
          .line 
            width: 100% !important
        &:nth-child(2), &:nth-child(4)
          flex: 1 1 auto
          @extend .row
          align-items: center
          .txt
            font-family: 'JackDaniels CondensedBold'
            font-size: 50px
            +mobile-small()
              font-size: 35px                
          .line
            height: 7px
            border-top: 1px solid #4c4d4d
            border-bottom: 1px solid #4c4d4d
            width: 35%

          
