@import 'tools/_mixins'
@import 'tools/_helpers'
@import 'tools/_media_queries'
@import 'tools/_normalize'
@import 'tools/_settings'
#places
  .container
    .header
      justify-content: space-between
      .back
        margin-left: 10px
      .logo
        font-family: 'JackDaniels CondensedBold'
        color: white 
        font-size: 24px
    .main 
      background: url('../images/bg.jpg') repeat-y 
      background-size: cover
      height: calc(100vh - 62px)
      @extend .column
      .flex-item
        display: flex
        flex: 14 1 auto
        display: flex 
        text-decoration: none
        color: white
        img 
          height: 20vh
          +mobile-small()
            height: 17vh
        .txt
          margin: auto
          text-align: center
          .italic
            font-family: 'SPSL Clarendon'
            font-style: italic
            font-size: 25px
          .text
            @extend .row
            align-items: baseline
            font-family: 'SPSL Clarendon'
            font-weight: bold
            span 
              margin: 0px 10px 0px 10px
              line-height: 40px
              font-size: 40px
              +mobile-small()
                font-size: 35px
            svg

              padding-top: 10px
              +mobile-small()
                width: 20px
              &:last-of-type
                transform: rotate(180deg)
                padding: 0
                padding-bottom: 10px

        &:nth-child(2)
          flex: 1 1 auto
          @extend .row
          align-items: center 
          .line
            height: 7px
            border-top: 1px solid #4c4d4d
            border-bottom: 1px solid #4c4d4d
            width: 35%
          .txt
            font-size: 35px
            font-family: 'JackDaniels CondensedBold'
            