@import 'tools/_mixins'
@import 'tools/_helpers'
@import 'tools/_media_queries'
@import 'tools/_normalize'
@import 'tools/_settings'

#calculate-form
  .container
    .header
      justify-content: space-between
      .back
        margin-left: 10px
      .logo
        font-family: 'JackDaniels CondensedBold'
        color: white 
        font-size: 24px
    .main 
      min-height: calc(100vh - 63px)
      background: url('../images/bg.jpg') repeat-y 
      background-size: cover
      height: 100%
      @extend .column
      justify-content: center
      align-items: center
      .form
        
        color: white
        
        font-family: 'JackDaniels CondensedBold'
        font-size: 20px
        @extend .column
        align-items: center
        .counter 
          margin-top: 20px
          width: 100%
          @extend .row
          align-items: center
          span 
            font-size: 70px
            line-height: 70px
        .button
          margin-top: 10px
          padding: 20px 0 15px 0
          width: 100%
          text-align: center
          font-size: 35px
          background: #E19541
          border-radius: 10px
          color: white 
          text-decoration: none

      .form2
        margin-top: 30px
