@import 'tools/_mixins'
@import 'tools/_helpers'
@import 'tools/_media_queries'
@import 'tools/_normalize'
@import 'tools/_settings'
#recipes-meat
  .container
    .spanner
      font-family: 'SPSL Clarendon'
      font-style: italic
      font-weight: bold
      font-size: 35px
      line-height: 35px
      +mobile-small()
        font-size: 30px
    .header
      justify-content: space-between
      .back
        margin-left: 10px
      .logo
        font-family: 'JackDaniels CondensedBold'
        color: white 
        font-size: 24px
    .main 
      @extend .row
      flex-wrap: wrap
      .flex-item
        width: 50%
        height: 318px
        border-bottom: 1px solid #4c4d4d
        position: relative
        color: white
        text-decoration: none
        @extend .column
        justify-content: flex-start
        align-items: center
        +mobile-small()
          height: 220px
        svg 
          min-width: 20px
          max-width: 20px
          margin-right: 5px
          +mobile-small()
            min-width: 10px
            max-width: 10px
          &:nth-of-type(2)
            transform: rotate(180deg)
            margin: 0 
            margin-left: 5px
        .title
          @extend .row
          align-items: center
          margin-top: Rem(40)
          font-family: 'SPSL Clarendon'
          font-size: 25px
          font-style: italic
          font-weight: bold
          line-height: 30px
          z-index: 2
          +mobile-small()
            font-size: 16px
            margin-top: Rem(20)
        .txt
          z-index: 2
          font-family: 'SPSL Clarendon'
          font-size: 28px
          line-height: 34px
          @extend .row
          font-weight: normal
          +mobile-small()
            font-size: 18px
        .image
          position: absolute
          width: 100%
          height: 100%
          top: 0
          left: 0
          background: url('../images/rec-meat1.png') no-repeat center center
          background-size: cover
        &:nth-of-type(2n)  
          border-left: 1px solid #4c4d4d
        &:nth-of-type(2)
          .title
            font-weight: normal 
            font-style: normal
          .txt
            font-weight: bold 

          .image
            background: url('../images/rec-meat2.png') no-repeat center center
            background-size: cover
        &:nth-of-type(3)
          .txt
            
            font-weight: bold
            +mobile-small()
              font-size: 15px
          .title
            border-bottom: 3px solid white
            font-style: normal

          .image
            background: url('../images/rec-meat3.png') no-repeat center center
            background-size: cover
        &:nth-of-type(4)
          .image
            background: url('../images/rec-meat4.png') no-repeat center center
            background-size: cover
        &:nth-of-type(5)
          .image
            background: url('../images/rec-meat5.png') no-repeat center center
            background-size: cover
        &:nth-of-type(6)
          .image
            background: url('../images/rec-meat6.png') no-repeat center center
            background-size: cover
          .txt
            
            font-weight: bold
            +mobile-small()
              font-size: 15px
          .title
            border-bottom: 3px solid white
            font-style: normal
    