@import 'tools/_mixins'
@import 'tools/_helpers'
@import 'tools/_media_queries'
@import 'tools/_normalize'
@import 'tools/_settings'
#recipes
  .container
    .spanner
      font-family: 'SPSL Clarendon'
      font-style: italic
      font-weight: bold
      font-size: 35px
      line-height: 35px
      +mobile-small()
        font-size: 30px
    .header
      justify-content: space-between
      .back
        margin-left: 10px
      .logo
        font-family: 'JackDaniels CondensedBold'
        color: white 
        font-size: 24px
    .main 
      background: url('../images/bg.jpg')
      background-size: cover
      min-height: 1020px
      @extend .column
      justify-content: flex-start
      .flex-item
        position: relative
        min-height: Rem(250)
        border-bottom: 1px solid #4c4d4d
        &:not(:first-of-type) 
          border-top: 1px solid #4c4d4d
          margin-top: 7px
        .cow 
          position: absolute
          left: 50%
          top: 50%
          transform: translate(-50%, -50%)
        .txt
          text-align: center
          position: absolute
          left: 50%
          top: 50%
          transform: translate(-50%, -50%)
          font-size: 25px
          line-height: 25px
          font-family: 'Motlow'
          color: white

          span 
            
            font-size: 40px
            line-height: 40px
            
            &:last-of-type
              font-family: 'SPSL Clarendon'
              font-weight: bold
              font-size: 50px
              line-height: 60px
              +mobile-small()
                font-size: 40px

          svg 
            width: 20px
            height: 20px
            margin-right: 10px
            vertical-align: middle
            &:last-of-type
              transform: rotate(180deg)
              margin: 0 
              margin-left: 10px