@import 'tools/_mixins'
@import 'tools/_helpers'
@import 'tools/_media_queries'
@import 'tools/_normalize'
@import 'tools/_settings'
#recipes-burger
  .container
    .spanner
      font-family: 'SPSL Clarendon'
      font-style: italic
      font-weight: bold
      font-size: 35px
      line-height: 35px
      +mobile-small()
        font-size: 30px
    .header
      justify-content: space-between
      .back
        margin-left: 10px
      .logo
        font-family: 'JackDaniels CondensedBold'
        color: white 
        font-size: 24px
        +mobile-small()
          font-size: 14px
    .main 
      background: url('../images/bg.jpg') repeat-y 
      background-size: cover
      height: 100%
      @extend .row
      flex-wrap: wrap
      .flex-item
        width: 50%
        height: 318px
        border-bottom: 1px solid #4c4d4d
        position: relative
        color: white
        text-decoration: none
        background: black
        @extend .column
        justify-content: flex-start
        align-items: center
        +mobile-small()
          height: 220px
        svg 
          min-width: 20px
          max-width: 20px
          margin-right: 5px
          +mobile-small()
            min-width: 10px
            max-width: 10px
          &:nth-of-type(2)
            transform: rotate(180deg)
            margin: 0 
            margin-left: 5px
        .title
          @extend .row
          align-items: center
          margin-top: Rem(40)
          font-family: 'SPSL Clarendon'
          font-size: 25px
          font-style: italic
          font-weight: bold
          line-height: 30px
          z-index: 2
          +mobile-small()
            font-size: 18px !important
            
            line-height: 20px !important

        .txt
          z-index: 2
          font-family: 'SPSL Clarendon'
          font-size: 28px
          line-height: 34px
          @extend .row
          font-weight: normal
          +mobile-small()
            font-size: 16px !important
            line-height: 25px !important

        .image
          position: absolute
          width: 100%
          height: 100%
          top: 0
          left: 0
          background: url('../images/rec-burger.png') no-repeat center center
          background-size: cover
        &:nth-of-type(1n+1)  
          border-right: 1px solid #4c4d4d
        &:nth-of-type(2n)  
          border-right: none
        &:nth-of-type(1)
          
          
          .title
            font-family: 'SPSL Clarendon'
            
            font-style: normal
            font-weight: normal
            font-size: 23px
            +mobile-small()
              font-size: 12px !important
          .txt
            font-family: 'JackDaniels CondensedBold'
            line-height: 45px
            align-items: center
            font-size: 45px

        &:nth-of-type(2)
          .title
            font-family: 'SPSL Clarendon'
            
            border-bottom: 3px solid white
            font-style: normal 
            font-weight: bold
            font-size: 30px
            line-height: 30px
          .txt
            font-family: 'JackDaniels CondensedBold'
            line-height: 45px
      
          .image
            background: url('../images/rec-burger2.png') no-repeat center center
            background-size: cover
        &:nth-of-type(3)
        
          .title
            font-family: 'SPSL Clarendon'
            
            border-bottom: 3px solid white
            font-style: normal 
            font-weight: bold
            font-size: 30px
            line-height: 30px
          .txt
            font-family: 'JackDaniels CondensedBold'
            line-height: 45px

          .image
            background: url('../images/rec-burger3.png') no-repeat center center
            background-size: cover
        &:nth-of-type(4)
          .image
            background: url('../images/rec-burger4.png') no-repeat center center
            background-size: cover
          .title
            font-family: 'SPSL Clarendon'
            
            border-bottom: 3px solid white
            font-style: normal 
            font-weight: bold
            font-size: 30px
            line-height: 30px
          .txt
            font-family: 'JackDaniels CondensedBold'
            line-height: 45px
        &:nth-of-type(5)
          .image
            background: url('../images/rec-burger5.png') no-repeat center center
            background-size: cover
            
          .title
            border-bottom: 3px solid white
            border-top: 3px solid white
            padding-top: Rem(3)
            font-style: normal
            font-size: 40px
            line-height: 40px
            font-family: 'JackDaniels CondensedBold'
        &:nth-of-type(6)
          .image
            background: url('../images/rec-burger6.png') no-repeat center center
            background-size: cover
          .title
            border-bottom: 3px solid white
            border-top: 3px solid white
            padding-top: Rem(3)
            font-style: normal
            font-size: 40px
            line-height: 40px
            font-family: 'JackDaniels CondensedBold'