@import 'tools/_mixins'
@import 'tools/_helpers'
@import 'tools/_media_queries'
@import 'tools/_normalize'
@import 'tools/_settings'
#aboutbbq
  .container
    .header
      justify-content: space-between
      .back
        margin-left: 10px
      .logo
        font-family: 'JackDaniels CondensedBold'
        color: white 
        font-size: 24px
        +mobile-small()
          font-size: 20px
    .main 
      background: url('../images/bg.jpg')
      background-size: cover
      min-height: 1020px
      @extend .column
      justify-content: flex-start
      .flex-item
        height: Rem(200)
        text-decoration: none
        border-bottom: 1px solid #4c4d4d
        &:not(:first-of-type) 
          border-top: 1px solid #4c4d4d
          margin-top: 7px
        &:nth-of-type(3)
          .txt
            .text
              order: -1
        &:nth-of-type(4)
          .txt
            .text
              order: -1
        svg 
          width: 20px
          height: 20px
          margin-right: 10px
          vertical-align: middle
          &:last-of-type
            transform: rotate(180deg)
            margin: 0 
            margin-left: 10px
        .txt
          height: 100%
          font-family: 'SPSL Clarendon'
          @extend .column
          justify-content: center
          align-items: center
          color: white
          .title
            @extend .row
            align-items: center
            letter-spacing: -1px
            color: #E19541
            font-style: italic
            font-weight: bold
            font-size: 30px
            line-height: 35px
            +mobile-small()
              font-size: 25px
          .text
            font-size: 55px
            font-weight: bold
            line-height: 50px
            +mobile-small()
              font-size: 40px
